@import url(https://fonts.googleapis.com/css?family=Raleway:400,700,900,400italic,700italic,900italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-link {
  color: #61dafb;
}

.log-in-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: white;
}

.logo{
  height: 500px;
}

*,
:before,
:after {
    box-sizing: border-box;
}

body {
    background-color: #fdf9fd;
    color: #011a32;
    font: 16px/1.25 'Raleway', sans-serif;
    text-align: center;
}

#wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 80em;
}

#container {
    display: flex;
    flex-direction: column;
    float: left;
    justify-content: center;
    width: 100%;
}

h1 {
    -webkit-animation: text-shadow 2.5s ease-in-out infinite;
            animation: text-shadow 2.5s ease-in-out infinite;
    font-size: 3em;
    font-weight: 900;
    line-height: 1;
}

h1:hover {
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

a {
    color: #024794;
}

a:hover {
    text-decoration: none;
}

@-webkit-keyframes text-shadow {
    0% {  
        -webkit-transform: translateY(0);  
                transform: translateY(0);
        /* text-shadow: 
            0 0 0 #0c2ffb, 
            0 0 0 #2cfcfd, 
            0 0 0 #fb203b, 
            0 0 0 #fefc4b; */
    }

    20% {  
        -webkit-transform: translateY(-1em);  
                transform: translateY(-1em);
        /* text-shadow:  */
            /* 0 0.125em 0 #0c2ffb, 
            0 0.25em 0 #2cfcfd, 
            0 -0.125em 0 #fb203b, 
            0 -0.25em 0 #fefc4b; */
    }

    40% {  
        -webkit-transform: translateY(0.5em);  
                transform: translateY(0.5em);
        /* text-shadow: 
            0 -0.0625em 0 #0c2ffb, 
            0 -0.125em 0 #2cfcfd, 
            0 0.0625em 0 #fb203b, 
            0 0.125em 0 #fefc4b; */
    }
    
   60% {
        -webkit-transform: translateY(-0.25em);
                transform: translateY(-0.25em);
        /* text-shadow: 
            0 0.03125em 0 #0c2ffb, 
            0 0.0625em 0 #2cfcfd, 
            0 -0.03125em 0 #fb203b, 
            0 -0.0625em 0 #fefc4b; */
    }

    80% {  
        -webkit-transform: translateY(0);  
                transform: translateY(0);
        /* text-shadow: 
            0 0 0 #0c2ffb, 
            0 0 0 #2cfcfd, 
            0 0 0 #fb203b, 
            0 0 0 #fefc4b; */
    }
}

@keyframes text-shadow {
    0% {  
        -webkit-transform: translateY(0);  
                transform: translateY(0);
        /* text-shadow: 
            0 0 0 #0c2ffb, 
            0 0 0 #2cfcfd, 
            0 0 0 #fb203b, 
            0 0 0 #fefc4b; */
    }

    20% {  
        -webkit-transform: translateY(-1em);  
                transform: translateY(-1em);
        /* text-shadow:  */
            /* 0 0.125em 0 #0c2ffb, 
            0 0.25em 0 #2cfcfd, 
            0 -0.125em 0 #fb203b, 
            0 -0.25em 0 #fefc4b; */
    }

    40% {  
        -webkit-transform: translateY(0.5em);  
                transform: translateY(0.5em);
        /* text-shadow: 
            0 -0.0625em 0 #0c2ffb, 
            0 -0.125em 0 #2cfcfd, 
            0 0.0625em 0 #fb203b, 
            0 0.125em 0 #fefc4b; */
    }
    
   60% {
        -webkit-transform: translateY(-0.25em);
                transform: translateY(-0.25em);
        /* text-shadow: 
            0 0.03125em 0 #0c2ffb, 
            0 0.0625em 0 #2cfcfd, 
            0 -0.03125em 0 #fb203b, 
            0 -0.0625em 0 #fefc4b; */
    }

    80% {  
        -webkit-transform: translateY(0);  
                transform: translateY(0);
        /* text-shadow: 
            0 0 0 #0c2ffb, 
            0 0 0 #2cfcfd, 
            0 0 0 #fb203b, 
            0 0 0 #fefc4b; */
    }
}

@media (prefers-reduced-motion: reduce) {
    * {
      -webkit-animation: none !important;
              animation: none !important;
      -webkit-transition: none !important;
      transition: none !important;
    }
}
